body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

header {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

h1 {
  margin: 0;
}


button {
  background-color: #fff;
  border: none;
  color: #4CAF50;
  padding: 10px 15px;
  margin-left: 10px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #ddd;
}


#userInfo {
  display: flex;
  align-items: center;
}


#productsContainer {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  padding: 20px;
}


.product {
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 15px;
  text-align: center;
  transition: transform 0.3s;
}

.product:hover {
  transform: scale(1.05);
}

.product img {
  max-width: 100%;
  border-radius: 5px;
}


#pagination {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

#pagination button {
  margin: 0 5px;
}


#cart {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  margin: 20px;
}

#cartItems {
  margin-bottom: 20px;
}

#totalPrice {
  font-weight: bold;
}


#productDetails {
  padding: 20px;
  background-color: white;
  border-radius: 5px;
  margin: 20px;
  text-align: center;
}


#searchInput {
  margin-top: 10px;
  width: 300px; 
  padding: 10px; 
  border: 1px solid #ccc; 
  border-radius: 5px; 
  font-size: 16px; 
  transition: border-color 0.3s; 
}


#searchInput:focus {
  border-color: #007BFF; 
  outline: none;
}


#searchInput::placeholder {
  color: #999;
  font-style: italic; 
}


div {
  margin: 20px 0; 
  font-family: Arial, sans-serif; 
}


label {
  font-size: 16px; 
  margin-right: 10px; 
  color: #333; 
}


#itemsPerPageSelect {
  padding: 10px; 
  border: 1px solid #ccc; 
  border-radius: 5px; 
  font-size: 16px; 
  background-color: #fff; 
  transition: border-color 0.3s;
}


#itemsPerPageSelect:focus {
  border-color: #007BFF; 
  outline: none; 
}

